<template>
  <div class="overall">
    <van-sticky>
      <header class="head">
        <div class="head-title">
          <van-icon name="arrow-left" size="23" @click="$router.go(-1)" />
          <div class="search">
            <!-- <transition name="fade"> -->
            <i class="search_i" v-if="searchShow" @click="searchShow = false">
              <span class="span_img"><img src="../../assets/image/hotel_search.png" alt="" /></span>
              <span>search</span>
            </i>
            <input class="input" type="text" v-model="search_name" v-else />
            <!-- <div class="input" contenteditable v-else>
            
          </div> -->
            <!-- </transition> -->
          </div>
          <van-icon @click="search" class="search_img hotelSearchImg" name="search" size="19" color="#A0CEF0" v-if="!searchShow" />

        </div>
      </header>
    </van-sticky>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :head-height="80">
      <!-- 下拉提示 -->
      <template #pulling>
        <span class="pull_text">下拉即可刷新/Pull down to refresh</span>
      </template>

      <!-- 释放提示 -->
      <template #loosing>
        <span class="pull_text">释放即可刷新/Releasing can refresh</span>
      </template>
      <!-- 加载提示 -->
      <template #loading>
        <van-loading type="spinner" size="24px">loading...</van-loading>
      </template>
      <article>
        <van-list v-model:loading="hotellist_loading" :finished="hotellist_end" finished-text="没有更多了/No more" @load="onLoad" loading-text="loading...">
          <!-- :error.sync="$store.state.hotellist.hotellist_error" error-text="请求失败，点击重新加载/Request failed, click reload" -->
          <section v-for="(item, i) in hotellist" :key="i" @click="hotelExpansion(item, i)">
            <div class="hotel_list_content">
              <div class="hotel_item_img">
                <img :src="
                    item.HotelInfo.Images &&
                    item.HotelInfo.Images.Image[0].Locations.Location[0].Text
                      ? item.HotelInfo.Images.Image[0].Locations.Location[0]
                          .Text
                      : 'https://foto.hrsstatic.com/fotos/0/3/256/256/80/FFFFFF/http%3A%2F%2Ffoto-origin.hrsstatic.com%2Ffoto%2F8%2F8%2F6%2F6%2F%2Fteaser_886671.jpg/s0Amhkg9TMTGSuYTSfpC1w%3D%3D/1024%2C630/6/Ji_Hotel_Jidi_Road_Hotel-Shanghai-Hotel_outdoor_area-3-886671.jpg'
                  " alt="" />
              </div>
              <div class="hotel_item_detail">
                <div class="hotel_item_det_title">
                  <div>
                    <span>{{ item.HotelInfo.Detail.Name }}</span><br />
                    <span style="font-size: 12px; color: #818181">{{
                      item.HotelInfo.Detail.Address
                    }}</span>
                    <!-- <span>{{item.HotelInf.Detail.}}</span> -->
                  </div>
                  <div class="hotel_item_det__star">
                    <span v-for="star in Number(
                        item.HotelInfo.Detail.StarRate &&
                          item.HotelInfo.Detail.StarRate != '0'
                          ? item.HotelInfo.Detail.StarRate
                          : item.HotelInfo.Detail.Category
                      )" :key="star">
                      <img src="../../assets/image/StarRate.png" alt="" />
                    </span>
                  </div>
                </div>
                <div class="hotel_item_det_more">
                  <span class="map" @click.stop="view_map(item.HotelInfo.Detail)">
                    查看地图/View Map
                  </span>
                  <!-- <div class="more_img">
                  <span><img src="../../assets/image/wf.png" alt="" /></span>
                  <span><img src="../../assets/image/yan.png" alt="" /></span>
                  <span><img src="../../assets/image/p.png" alt="" /></span>
                  <span><img src="../../assets/image/zc.png" alt="" /></span>
                  
                </div> -->
                  <div class="hotel_all">
                    <span><img src="../../assets/image/hotel_all.png" /></span>
                  </div>
                  <!--  -->
                </div>
                <div class="price">￥{{ toMoney(item.LowRate) }}</div>
              </div>
            </div>
          </section>
        </van-list>
      </article>
    </van-pull-refresh>

    <transition name="bottool">
      <div class="bottom-toolbars" v-if="bomToolFlag">
        <van-grid :border="false" :column-num="3">
          <van-grid-item :text="store_sort(0)" @click="Sort(0)">
            <div v-if="store_sort(0) == '价格排序'">
              <van-icon name="gold-coin-o" size="30" />
              <p>
                <span>价格排序</span>
                <span>Price</span>
              </p>
            </div>
            <div v-else-if="store_sort(0) == '价格升序'">
              <van-icon name="after-sale" size="30" color="#4086EA" />
              <p class="bot_too_col">
                <span>价格升序</span>
                <span>Price Asc</span>
              </p>
            </div>
            <div v-else-if="store_sort(0) == '价格降序'">
              <van-icon name="refund-o" size="30" color="#4086EA" />
              <p class="bot_too_col">
                <span>价格降序</span>
                <span>Price Desc</span>
              </p>
            </div>
          </van-grid-item>
          <van-grid-item :text="store_sort(1)" @click="Sort(1)">
            <div v-if="onSolt(store_sort(1)) == 0">
              <van-icon name="fire-o" size="30" />
              <p>
                <span>星级排序</span>
                <span>Star</span>
              </p>
            </div>
            <div v-else-if="onSolt(store_sort(1)) == 1">
              <van-icon name="fire-o" size="30" color="#4086EA" />
              <p class="bot_too_col">
                <span>星级升序</span>
                <span>Star Asc</span>
              </p>
            </div>
            <div v-else-if="onSolt(store_sort(1)) == 2">
              <van-icon name="fire-o" size="30" color="#4086EA" />
              <p class="bot_too_col">
                <span>星级升序</span>
                <span>Star Asc</span>
              </p>
            </div>
          </van-grid-item>
          <!-- <van-grid-item
          icon="underway-o"
          :text="store_sort(1)"
          @click="Sort(1)"
        /> -->
          <van-grid-item @click="filtrateShow = true">
            <div>
              <van-icon name="filter-o" size="30" />
              <p>
                <span>支付筛选</span>
                <span>Price</span>
              </p>
            </div>
          </van-grid-item>
        </van-grid>
      </div>
    </transition>

    <div class="filtrate_box">
      <van-action-sheet v-model:show="filtrateShow" close-on-click-action close-icon="none" title="筛选/Filtrate">
        <hr class="delay-line" />
        <div class="filtrate">
          <van-tree-select :items="filtrates" v-model:main-active-index="activeIndex">
            <!-- <template #item>
              <div>{{filtrates}}</div>
          </template> -->
            <template #content>
              <div v-if="active === 0">
                <div class="filtrate-content">
                  <van-checkbox-group v-model="result" ref="checkboxGroup">
                    <div class="unlimited_box">
                      <span class="unlimited" :class="[isUnlimited ? 'onUnlimited' : 'noUnlimited']" @click="handleUnlimited">
                        不限/Default
                      </span>
                    </div>

                    <van-cell-group>
                      <van-cell v-for="(item, index) in list" clickable :key="index" :title="item.title" @click="toggle(index)">
                        <template #right-icon>
                          <van-checkbox :name="item" ref="checkboxes" checked-color="#2062b3" />
                        </template>
                      </van-cell>
                    </van-cell-group>
                  </van-checkbox-group>
                </div>
              </div>
            </template>
          </van-tree-select>
          <div style="margin: 0 30px; display: flex; justify-content: space-between;">
            <van-button class="creal" plain icon="delete-o" type="info" @click="empty">清空</van-button>
            <van-button class="confirm" icon="passed" type="info" @click="start_filter">确定</van-button>
          </div>
        </div>
      </van-action-sheet>
    </div>
    <div class="map_pup">
      <!-- <van-dialog v-model="mapShow" title="地图" show-cancel-button width="80%">

      </van-dialog> -->
      <!-- 通过函数指定挂载位置 -->
      <!-- <van-popup v-model="mapShow" :get-container="getContainer"  :style="{ height: '80%' }">
          <div class="mpa_box"></div>
      </van-popup> -->
      <van-popup v-model:show="mapShow" position="top" :style="{ height: '85%' }">
        <div class="map_box">
          <Map @mapCloseValue="mapCloseValue" :showMap="mapShow" :listData="mapData"></Map>
        </div>
      </van-popup>
    </div>
    <ScrollTop></ScrollTop>
  </div>
</template>

<script>
import Map from "../children/map.vue";
import ScrollTop from "../scrolltopPage/ScrollTop.vue";
import axios from '../../utils/axiosLogin.js'
export default {
  data () {
    let a = JSON.parse(sessionStorage.getItem("search_hotel"));

    return {
      bomToolFlag: true,
      isLoading: false,
      HotelData: [],
      sendjson: a,
      StarRate: null,
      sortl: ["价格", "星级"],
      filtrateShow: false,
      activeIndex: 0,
      active: 0,
      filtrates: [{ text: "支付方式/Payment" }],
      result: [],
      isUnlimited: true,
      list: [
        { title: "预付/Prepay", e: "预付" },
        { title: "前台现付/SelfPay", e: "前台现付" },
      ],
      mapShow: false,
      mapData: [{}],

      getMapData: null,
      searchShow: true,
      search_name: "",
      // sortS: null,


      // vuex
      hotellist: [],
      hotellist_ind: 1,
      hotellist_end: false,
      hotellist_loading: false,
      hotellist_sort_0: null,
      hotellist_sort_1: null,
      hotellist_sort_2: null,
      hotellist_error: false,
      hotellist_StarRate: [],
      sort_asc: null,
      hotellist_HotelName: "",

      Seakey: null,
    };
  },
  created () {
    // this.get_hotel_list();
    // this.sendjson.Sort = "StarRankDesc"; //默认星级排序
    this.sendjson.Sort = "Default";

    // console.groupEnd();

    // this.onSolt()
  },
  mounted () {
    //挂载完成
    window.addEventListener("scroll", this.scrollH);
  },

  destroyed () {
    //销毁后
    window.removeEventListener("scroll", this.scrollH);
  },
  watch: {
    // mapShow(e) {
    //   console.log(this.getMapData,this.mapData);
    //   this.getMapData = this.mapData;
    // },
  },
  computed: {
    store_sort: function () {
      return (x) => {
        let c = this[`hotellist_sort_${x}`];
        let v = c === null ? "排序" : c === false ? "降序" : "升序";
        // if(this.$store.state.hotellist["hotellist_sort_1"]==true){
        //     return this.sortl[x]+"555"
        // }
        // if (this.$store.state.hotellist["hotellist_sort_1"] == true) {
        //   console.log(5555, this.sortl[x]);

        //   return;
        // }
        // console.log(this.$store.state.hotellist["hotellist_sort_1"]);
        // console.log(this.sendjson.Sort,this.$store.state.hotellist["hotellist_sort_1"]);
        return this.sortl[x] + v;
      };
    },
  },

  methods: {
    scrollH () {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;

      var scroll = scrollTop - that.scrollTop;
      that.scrollTop = scrollTop;

      if (that.scrollTop > 120 && scroll > 0) {
        that.bomToolFlag = false;
      } else {
        that.bomToolFlag = true;
      }
    },

    mapCloseValue (val) {
      this.mapShow = val;
    },
    onSolt (x) {
      let sort = { 星级排序: 0, 星级降序: 1, 星级升序: 2 };
      let obj = sort[x];
      // console.log(sort[this.store_sort(1)])
      return obj;
    },
    async gethotellist (b) {
      b.pageIndex = this.hotellist_ind
      if (this.hotellist_sort_0 !== null) {
        b.Sort = this.hotellist_sort_0 ? "RateAsc" : "RateDesc" //true=升序/false=降序
      } else if (this.hotellist_sort_1 !== null) {
        //Default
        b.Sort = this.hotellist_sort_1 ? "StarRankDesc" : "Default" //true=升序/false=降序
        b.sort_asc = this.hotellist_sort_1 ? true : false
      }

      //Prepay预付，SelfPay前台自费
      let so = this.hotellist_sort_2
      if (so == null || so == -1) {
        b.PaymentType = "All"
      } else if (so == 0) {
        b.PaymentType = "Prepay"
      } else if (so == 1) {
        b.PaymentType = "SelfPay"
      }

      // if (a.Seakey) return;

      this.hotellist_loading = true

      let g = await axios({
        method: "POST",
        // url: "https://cwellsapi.casonline.cn/apiservice/hotelservice.asmx/getKPLHotellist",
        url: "https://timas.efasco.com/HotelService.asmx/getELONGHotellist",
        data: JSON.stringify(b),
      }).catch((error) => {
        b.catchN += 1
        showDialog({
          title: "提示/Notice",
          message: "请求失败，请检查您的网络/The request failed. Please check your network!",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        })
      })
      // console.log(g);
      let that = this
      // console.log(a.Seakey)
      this.hotellist_loading = false
      if (g.data.Result.Hotels == null) {
        // a.hotellist_error = true
        this.hotellist_HotelName = ""
        console.log(false)
        showDialog({
          title: "提示/Notice",
          message:
            "您所搜索的日期范围内酒店无房/Your desired hotel is not available for these travel dates!",
          theme: "round-button",
          confirmButtonText: "确定/Confirm",
          confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)",
        }).then(() => {
          this.$router.go(-1)
        })
      }
      if (typeof g.data == "object") {
        console.log("vuex中的g.data", g.data)
        if (this.hotellist_sort_1 !== null && b.sort_asc == true) {
          //低星级dialog.....
          // a.hotellist.sort((a,b)=>{
          //   console.log(a,b)
          // })
          // g.data.Result.Hotels.reverse()
          // console.log(g.data.Result.Hotels)
        }
        this.hotellist = this.hotellist.concat(g.data.Result.Hotels)
        this.hotellist_ind++
        this.hotellist_end = this.hotellist.length >= g.data.Result.Count
        // console.log(a.hotellist_end, a.hotellist.length, g.data.Result.Count)

        // a.hotellist.map((v, i) => {
        //   a.hotellist_StarRate = a.hotellist_StarRate.concat(v.HotelInfo.Detail.StarRate)
        //   //  console.log(a.hotellist_StarRate)
        // })
        try {
          this.hotellist.map((item) => {
            this.hotellist_StarRate = this.hotellist_StarRate.concat(item.HotelInfo.Detail.StarRate)
            // console.log("hotellist_StarRate", a.hotellist_StarRate)
            // console.log("a",a, b)
            this.hotellist_HotelName = ""
            //  console.log(this)
            this.hotellist_loading = false

            // sessionStorage.setItem('set_html', JSON.stringify(g.data))
          })
        } catch (error) {
          console.log(error)
        }
      }
    },
    onRefresh () {
      this.onLoad();
    },
    onLoad () {
      // console.log('sendjson', this.sendjson);
      if (this.sendjson.catchN == 5) {
        setTimeout(() => {
          this.isLoading = false;
        }, 3000);
        return;
      }
      this.gethotellist(this.sendjson)
      setTimeout(() => {
        this.isLoading = false;
      }, 3000);
    },
    getContainer () {
      return document.querySelector(".map_pup");
    },
    // async get_hotel_list() {
    //   this.upload();
    //   let data=this.sendjson
    //   // data.QueryType="HotelName"
    //   // data.QueryText="金"
    //   // console.log()
    //   let a = await this.$axios({
    //     method: "post",
    //     // url: "https://cwellsapi.casonline.cn/apiservice/hotelservice.asmx/getKPLHotellist",
    //     url: 'http://timas.efasco.com/HotelService.asmx/getELONGHotellist',
    //     data: JSON.stringify(data)
    //   });
    //   console.log(a);
    //   this.$set(this, "HotelData", a.data.Result.Hotels);
    //   this.$toast.clear();
    // },
    onFiltrate () { },
    hotelExpansion (d, i) {
      sessionStorage.setItem("hoteldata", JSON.stringify(d));
      this.$router.push({
        path: "/hotelExpansion",
        query: { elongid: d.HotelId },
      });
    },
    sort (b) {
      this[`hotellist_sort_${b}`] = !this[`hotellist_sort_ ${b}`]
      let c = b == 0 ? 1 : 0
      this[`hotellist_sort_${c}`] = null
    },
    removehotellist () {
      this.hotellist = []
      this.hotellist_ind = 1
      this.hotellist_end = false
    },
    Sort (x) {
      this.sort(x);
      this.removehotellist();
      this.gethotellist(this.sendjson);
    },
    // upload () {
    //   this.$toast.loading({
    //     message: "加载中...",
    //     duration: 0,
    //     forbidClick: true,
    //   });
    // },
    handleUnlimited () {
      this.isUnlimited = !this.isUnlimited;
      this.$refs.checkboxGroup.toggleAll(false);
    },
    toggle (index) {
      //    this.$refs.checkboxes[index].toggle()
      this.$refs.checkboxes[index].toggle();
      this.isUnlimited = false;

      //   console.log(index, this.$refs.checkboxes);
    },
    empty () {
      this.isUnlimited = false;
      this.$refs.checkboxGroup.toggleAll(false);
    },

    toMoney (num) {
      num = parseFloat(num);
      num = num + "";

      if (num.indexOf(".") > 0) {
        var x = num.split(".")[1];
        var y = num.split(".")[0];
        if (x.length >= 2) {
          return y + "." + x.substr(0, 2);
        } else if (x.length == 1) {
          return num + "0";
        } else if (x.length == 0) {
          return num + "00";
        }
      } else {
        return num + ".00"; //返回的是字符串23,245.12保留2位小数
      }
    },
    view_map (a) {
      this.mapShow = true;
      this.mapData[0] = {
        title: a.Name,
        data: a,
        BaiduLat: a.BaiduLat,
        BaiduLon: a.BaiduLon,
      };
    },
    set_sort_3 (b) {
      this.hotellist_sort_2 = b
    },
    start_filter () {
      //   console.log(this.sendjson, this.result);
      if (this.result.length == 0) {
        this.set_sort_3(-1);
      } else if (this.result.length == 1) {
        this.set_sort_3(this.result[0].e == "预付" ? 0 : 1);
      } else {
        this.set_sort_3(-1);
      }
      this.removehotellist();
      this.gethotellist(this.sendjson);

      this.filtrateShow = false;
      //   console.log(this.sendjson)
    },
    search () {
      // this.$store.commit("set_hotel_name", this.search_name);
      this.sendjson.queryText = this.search_name;
      this.removehotellist();
      this.gethotellist(this.sendjson);

    },
  },
  components: {
    Map,
    ScrollTop,
  },
  beforeRouteLeave (to, from, next) {
    console.log(to);
    if (to.path == '/index') {
      sessionStorage.removeItem('search_hotelx')
      console.log(11111);
    }
    next()
  }
};
</script>
<style lang="scss" scoped>
@import url("../../assets/css/transfer.css");
.bottool-enter-active {
  animation: fadeInUp 0.5s;
}

.bottool-leave-active {
  animation: fadeOutDown 0.5s;
}
.overall {
  .map_pup {
    .map_box {
      .map {
        .head {
          width: 95.5%;
          box-sizing: border-box;
        }
        // background-color: white;
      }
    }
  }
}
</style>
